	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>Contributor Experience Special Interest Group</h1>\n<p>Developing and sustaining a healthy community of contributors is critical to scaling the project and growing the ecosystem. We need to ensure our contributors are happy and productive, we need to break the commit-rate ceiling we hit in July 2015, and we need to get the monotonically growing PR merge latency and numbers of open PRs and issues under control.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/j/7658488911\">Wednesdays at 9:30 PT (Pacific Time)</a> (weekly). <a href=\"http://www.thetimezoneconverter.com/?t=9:30&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/1qf-02B7EOrItQgwXFxgqZ5qjW0mtfu5qkYIF1Hl4ZLI/\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/watch?v=EMGUdOKwSns&#x26;list=PL69nYSiGNLP2x_48wbOPO0vXQgNTm_xxr\">Meeting recordings</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Elsie Phillips (<strong><a href=\"https://github.com/Phillels\">@Phillels</a></strong>), CoreOS</li>\n<li>Paris Pittman (<strong><a href=\"https://github.com/parispittman\">@parispittman</a></strong>), Google</li>\n</ul>\n<h3>Technical Leads</h3>\n<p>The Technical Leads of the SIG establish new subprojects, decommission existing\nsubprojects, and resolve cross-subproject technical issues and decisions.</p>\n<ul>\n<li>Garrett Rodrigues (<strong><a href=\"https://github.com/grodrigues3\">@grodrigues3</a></strong>), Google</li>\n<li>Christoph Blecker (<strong><a href=\"https://github.com/cblecker\">@cblecker</a></strong>)</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/sig-contribex\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-contribex\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Fcontributor-experience\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>Subprojects</h2>\n<p>The following subprojects are owned by sig-contributor-experience:</p>\n<ul>\n<li>\n<p><strong>community</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/community/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/community/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>community-management</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/community/master/communication/OWNERS\">https://raw.githubusercontent.com/kubernetes/community/master/communication/OWNERS</a> <a href=\"https://raw.githubusercontent.com/kubernetes/community/master/events/OWNERS\">https://raw.githubusercontent.com/kubernetes/community/master/events/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>contributors-guide</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/community/master/contributors/guide/OWNERS\">https://raw.githubusercontent.com/kubernetes/community/master/contributors/guide/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>devstats</strong></p>\n<ul>\n<li>Owners:</li>\n<li>Phillels</li>\n</ul>\n</li>\n<li>\n<p><strong>k8s.io</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/k8s.io/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/k8s.io/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>mentoring</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/community/master/mentoring/OWNERS\">https://raw.githubusercontent.com/kubernetes/community/master/mentoring/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>repo-infra</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/repo-infra/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/repo-infra/master/OWNERS</a></li>\n</ul>\n</li>\n</ul>\n<h2>GitHub Teams</h2>\n<p>The below teams can be mentioned on issues and PRs in order to get attention from the right people.\nNote that the links to display team membership will only work if you are a member of the org.</p>\n<p>The google groups contain the archive of Github team notifications.\nMentioning a team on Github will CC its group.\nMonitor these for Github activity if you are not a member of the team.</p>\n<table>\n<thead>\n<tr>\n<th>Team Name</th>\n<th align=\"center\">Details</th>\n<th align=\"center\">Google Groups</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@kubernetes/sig-contributor-experience-bugs</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-contributor-experience-bugs\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-contributor-experience-bugs\">link</a></td>\n<td>Bug Triage and Troubleshooting</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-contributor-experience-feature-requests</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-contributor-experience-feature-requests\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-contributor-experience-feature-requests\">link</a></td>\n<td>Feature Requests</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-contributor-experience-misc-use-only-as-a-last-resort</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-contributor-experience-misc-use-only-as-a-last-resort\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-contributor-experience-misc-use-only-as-a-last-resort\">link</a></td>\n<td>General Discussion</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-contributor-experience-pr-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-contributor-experience-pr-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-contributor-experience-pr-reviews\">link</a></td>\n<td>PR Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-contributor-experience-proposals</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-contributor-experience-proposals\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-contributor-experience-proposals\">link</a></td>\n<td>Design Proposals</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-contributor-experience-test-failures</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-contributor-experience-test-failures\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-contributor-experience-test-failures\">link</a></td>\n<td>Test Failures and Triage</td>\n</tr>\n</tbody>\n</table>\n<!-- BEGIN CUSTOM CONTENT -->\n<!-- END CUSTOM CONTENT -->"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-contributor-experience/"}}

